import { Component, Host, h, State, Listen, Prop } from '@stencil/core';

//TODO Change tag property to final name of the component
@Component({
  tag: 'feedback-container',
  styleUrl: 'feedback-container.css',
  shadow: true,
})
export class FeedbackContainer {
  @Prop() componentKey: string;
  @Prop() shadow: boolean;

  @State() hidden: boolean;
  @State() feedbackCreated: boolean;

  cssClass: string;

  @Listen('cancelClicked')
  cancelClickedHandler(event: CustomEvent<boolean>) {
    this.hidden = event.detail;
  }

  @Listen('feedbackCreated')
  feedbackCreatedHandler(event: CustomEvent<boolean>) {
    this.feedbackCreated = event.detail;
  }
  constructor() {
    this.hidden = true;
    this.feedbackCreated = false;
    this.cssClass = '';
    if (this.shadow === true) {
      this.cssClass = this.cssClass.concat('shadow ');
    }
  }

  getFeedbackCreated() {
    if (this.feedbackCreated) {
      return (
        <div class='ripple-back' id='ripple-loader'>
          <p class='thank-you ripple-loader'>
            <span class='heart-icon'>&#9829;</span> Thanks for the feedback!
          </p>
        </div>
      );
    } else {
      return this.getFeedbackComponent();
    }
  }

  getFeedbackComponent() {
    if (this.hidden) {
      return (
        <button
          class={this.cssClass}
          onClick={() => this.showFeedbackComponent()}
        >
          Feedback?
        </button>
      );
    } else {
      return (
        <feedback-component
          componentKey={this.componentKey}
          shadow={this.shadow}
        ></feedback-component>
      );
    }
  }

  render() {
    return <Host>{this.getFeedbackCreated()}</Host>;
  }
  showFeedbackComponent(): void {
    if (this.hidden) {
      this.hidden = false;
    } else {
      this.hidden = true;
    }
  }
}
